html {
    scroll-behavior: smooth;
  }
  
  body {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
  }
  
  img {
    display: block;
    max-width: 100%;
    height: auto;
  }
  
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p {
    margin-top: 0;
    margin-bottom: 0;
  }
  
  ul,
  ol {
    margin-top: 0;
    margin-bottom: 0;
    padding-left: 0;
    list-style: none;
  }
  
  .button {
    cursor: pointer;
  }
  
  address {
    font-style: normal;
  }
  
  a {
    text-decoration: none;
    color: inherit;
  }

  .container{
    margin-left: 100px;
    margin-right: 100px;
  }